@import "animation.css";

@font-face {
	font-family: Righteous;
	src: local(Righteous), url(./fonts/Righteous-Regular.ttf) format("truetype");
}

html,
body,
#root {
	min-width: 320px;
	height: 100%;
}

body {
	margin: 0;
	padding: 0px;
}

.body {
	height: 100%;
	width: 100%;
	background-color: var(--secondary);
	overflow-x: hidden;
	animation: hideScrollbar 0s 3s forwards;
	transition: all 0.5s;
}

/* App */
.app {
	background-color: var(--secondary);
	margin: auto;
	max-width: 1200px;
	padding: 15px;
	transition: all 0.5s;
}

ul {
	display: flex;
	justify-content: right;
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	color: var(--primary);
	font-family: Righteous;
	font-size: 22px;
	transition: all 0.5s;
	opacity: 0;
	animation: showNavbar 1s 1.5s forwards;
}

li {
	display: block;
	text-align: center;
	text-decoration: none;
	margin: 20px 25px 25px 25px;
	cursor: pointer;
}

ul div {
	display: flex;
	flex-direction: row;
}

.option {
	font-size: 14px;
	margin: 25px 5px 25px 10px;
	display: flex;
}

.content {
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	gap: 40px;
}

.leftBar {
	width: 200px;
	height: 350px;
	display: flex;
	justify-content: center;
}

.profile {
	width: 200px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.profile p {
	font-family: Arial, Helvetica, sans-serif;
}

.profile h3 {
	text-align: center;
	color: var(--primary);
	transition: all 0.5s;
}

.profile p {
	color: var(--primary);
	margin: 5px;
	font-weight: bold;
	font-size: 16px;
	transition: all 0.5s;
}

.profile img {
	width: 150px;
	height: auto;
	border: 5px solid var(--primary);
	margin-bottom: 10px;
	transition: all 0.5s;
}

.icons {
	margin: 10px;
	display: flex;
	flex-direction: row;
	align-items: center;
	gap: 10px;
}

.icons a {
	cursor: pointer;
	color: var(--primary);
	font-size: 30px;
	transition: all 0.5s;
}

.icons a:hover {
	font-size: 50px;
}

/* Items */
.items {
	flex: 1 1 300px;
	min-width: 300px;
	opacity: 0;
	animation: itemMoveIn 1s 1s forwards;
}

.line {
	height: 5px;
	width: 100%;
	background-color: var(--primary);
	margin-bottom: 25px;
	transition: all 0.5s;
}

.item {
	background-color: var(--primary);
	padding: 15px;
	margin-bottom: 25px;
	transition: all 0.5s;
}

.item p {
	color: var(--secondary);
	font-family: Arial;
	transition: all 0.5s;
	margin: 10px 0 0 0;
	font-size: 18px;
}

.item img {
	width: auto;
	height: 200px;
	margin-top: 20px;
}

.BsDownload {
	margin-bottom: -2px;
}

/* Other */
p a {
	text-decoration: none;
}

a {
	color: var(--secondary);
	transition: all 0.5s;
}

a:hover,
li:hover {
	text-decoration: underline;
}

h2 {
	font-family: Righteous;
	margin: 0;
	color: var(--primary);
	transition: all 0.5s;
}

h3 {
	font-family: Righteous;
	margin: 0 0 10px 0;
	color: var(--secondary);
	transition: all 0.5s;
}

@import "mobileView.css";
